import { useStaticQuery, graphql } from "gatsby"

export const getSiteLogo = () => {
  const data  = useStaticQuery(
    graphql`
    query {
        file(relativePath: { eq: "logo.png" }) {
          childImageSharp {
            fixed(width: 400, height: 60) {
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    `
  )
  return data.file.childImageSharp
}
